import React, { Component } from 'react';
import { Route, Switch } from 'react-router-dom';
import StripsContainer from './containers/StripsContainer';
import StripInfoComponent from './components/StripInfo';

class App extends Component {
  render () {
    return (
      <div style={{padding:'10px'}}>
          <Switch>
            <Route 
              path="/stripInfo/:number"
              exact
              render={(props) =>(
                <StripInfoComponent id={props.match.params.number}></StripInfoComponent>
              )}/>
            <Route path="/" exact component={StripsContainer} />
          </Switch>
      </div>
    );
  }
}

export default App;
