import React, { Component } from 'react';
import { connect } from 'react-redux';
import axios from '../axios';
import * as stripActions from '../store/actions';
import {Container,Button, Row, Col, Toast} from 'react-bootstrap';

export class StripsContainer extends Component{
    constructor(props){
        super(props);
    }
    componentDidMount(){
        this.props.fetchAllStrips();
    }
    expandStrip(number){
        this.props.history.push(`/stripInfo/${number}`)
    }
    render(){
        return(
            <Container fluid>
                <Row>List of Strips</Row>
                <ul>
                    {this.props.strips.map((strip, i) => (
                        <li key={i} style={{cursor:'pointer'}} onClick={()=>this.expandStrip(strip.number)}> {strip.title} </li>
                    ))} 
                </ul>            
            </Container>
        )
    }
}
const mapStateToProps = state => {
    return {
        strips: state.strips
    };
}

const mapDispatchToProps = dispatch => {
    return {
        fetchAllStrips: () => dispatch(stripActions.fetchAllStrips()),
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(StripsContainer, axios );